.leftmost{
    margin-left: 5vw;
    margin-top: 14vh;
    max-width: -webkit-min-content;
    max-width: -moz-min-content;
    max-width: min-content;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.links p1{
    opacity: 0;
}
  .links{
    padding-top: 2vh;  
    padding-bottom: 2vh; 
    display: -ms-flexbox; 
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly; 
    justify-items: center;
    background-color: rgb(255, 255, 255);
    border-radius: 5px;
    padding-top: 4vh;
    padding-bottom: 8vh;
  }
  
  .links a {
    font-size: 12px;
    padding-top: 15px;
    text-decoration: none;
    font-family:sans-serif;
    font-weight: bold;
    margin-left: 1%;
    padding-left: 3px;
    padding-right: 3px;
    min-width: -webkit-min-content;
    min-width: -moz-min-content;
    min-width: min-content;
    margin-top: 10px;
    color: rgb(39, 39, 35);
  }


  .links hr{
    background: -webkit-gradient(linear, left top, right top, from(rgb(253, 253, 253)),color-stop(rgb(190, 189, 189)), to(rgb(255, 255, 255)));
    background: -webkit-linear-gradient(left, rgb(253, 253, 253),rgb(190, 189, 189), rgb(255, 255, 255));
    background: -o-linear-gradient(left, rgb(253, 253, 253),rgb(190, 189, 189), rgb(255, 255, 255));
    background: linear-gradient(to right, rgb(253, 253, 253),rgb(190, 189, 189), rgb(255, 255, 255));
    height: 1px;
    border-style: hidden;
  }
  .hoveranim:hover{
     color: rgb(5, 66, 5);
     opacity: .8;
  }

  
@media screen and (max-width: 800px) {
  .leftmost{
    margin-left: 0px;
  }
}


