.left{
    -ms-flex: .4 1;
        flex: .4 1;
    margin-top: 4vh;
    background-color:rgb(116, 136, 127) ;
    height: 87vh;
}
.profile{
    margin-left: .6vw;
    margin-top: 1.5vh;
    height: 84vh;
}

.photo{
    height: 80vh;
    position: relative;
    width: 100%;
    -webkit-animation: bounceInDown;
            animation: bounceInDown;
    -webkit-animation-duration: 1.5s;
            animation-duration: 1.5s;
}
.photo img{
    width: 100%;
}

.stylediv{
    background: white;
    color: black;
    position: relative;
    bottom: 16vh;
    height: -webkit-min-content;
    height: -moz-min-content;
    height: min-content;
    text-align: center;
    width: 100%;  
}

.stylediv::before{
 position: absolute;
 content: '';
 width: 100%;
 height: 80px;
 top: -100px;
 padding-bottom: 90px;
 left: 0;
 bottom: 0;
 background: white;
 -webkit-transform: skewY(20deg);
     -ms-transform: skewY(20deg);
         transform: skewY(20deg);
}
.stylediv::after{
    position: absolute;
    content: '';
    width: 100%;
    height: 80px;
    top: -100px;
    padding-bottom: 90px;
    right: 0;
    background: white;
    -webkit-transform: skewY(-20deg);
        -ms-transform: skewY(-20deg);
            transform: skewY(-20deg);
   }
   .info{
       position: absolute;
       top:55vh;
       width: 35.5vw;
       min-height: 41vh;
       background-color: white;
   }

   .info h2{
       color: rgb(39, 39, 35);
       font-family: cursive;
   }
   .info hr{
    background: -webkit-gradient(linear, left top, right top, from(rgb(253, 253, 253)),color-stop(rgb(185, 179, 179)), to(rgb(255, 255, 255)));
    background: -webkit-linear-gradient(left, rgb(253, 253, 253),rgb(185, 179, 179), rgb(255, 255, 255));
    background: -o-linear-gradient(left, rgb(253, 253, 253),rgb(185, 179, 179), rgb(255, 255, 255));
    background: linear-gradient(to right, rgb(253, 253, 253),rgb(185, 179, 179), rgb(255, 255, 255));
    height: 1px;
    border-style: hidden;
   }
   
   .infoanim{
    animation: mymove 5s infinite;
    -moz-animation: mymove 5s infinite;
    -webkit-animation: mymove 5s infinite;
    -o-animation: mymove 5s infinite;    
    color: rgb(5, 66, 5);
   }

   .iconanim{
    color: rgb(39, 39, 35);
    padding-left: 15px;
   }
   .iconanim:hover{
    color: rgb(5, 66, 5);
   }

   .bottom a{
       text-decoration: none;
      
   }
   .left-footer{   
       text-decoration: none;
       color: rgb(39, 39, 35);
     
   }
   .left-footer:hover{
    color: rgb(5, 66, 5);
   }
   .info p1{
       display: none;
       font-size: 11px;
       color: rgb(5, 66, 5);
   }






   @media screen and (max-width: 800px) {

    .left{
        -ms-flex: .35 1;
            flex: .35 1;
        margin-top: 15vh;
        background-color: transparent;
    }
    .photo{
        margin-bottom: 30px;
        height: 270px;
        width: 125px;
    }
    .photo img{
       
        width: 100%;
    }
    .profile{
        margin-top: 10vh;
        max-height: 90vh;
    }
    .stylediv{
        margin-top:-145px;
        width: 125px;
    }
   .info{
      min-height: 210px;
      width: 125px;
      margin-top: -100px;
      
   }
   .info h2{
      
       font-size: 15px !important;
   }
   .info p{
       display: none !important;
   }
   .info p1{
       display: block !important;
   }
 
   .bottom{
       padding-top: 30px;
   }

   .left-footer{
       padding-bottom: -30px;
       font-size: 14px;
   }
   .iconanim{
    
    padding-left: 3px;
   }
      
   }
   @media screen and (max-width: 1000px){
    .profile{
        margin-top: 10vh;
        max-height: 60vh;
    }
   .info{
      max-height: 10px;
      max-width: 296px;
      margin-top: -100px;
   }
   .photo{
    margin-bottom: 30px;
    height: 430px;
}
.stylediv{
    margin-top:-145px
}

.info h2{
    font-size: 23px;
}
.info p1{
    display: none;
}
.info p{
    font-size: 14px;
    display: block;
}

   }






















   @keyframes mymove {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {padding-left: 340px ;}
    to {padding-left:0 ;}
  }
   @-webkit-keyframes mymove {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {padding-left: 340px ;}
    to {padding-left:0 ;}
  }


