.home{
   -ms-flex: .54 1;
       flex: .54 1;
   margin-top: 8vh;
   max-height: 84vh;
   overflow-y: scroll;
   background-image: -webkit-linear-gradient(130deg,#ffffff,#e3f0e2, #ffffff ) ;
   background-image: -o-linear-gradient(130deg,#ffffff,#e3f0e2, #ffffff ) ;
   background-image: linear-gradient(-40deg,#ffffff,#e3f0e2, #ffffff ) ;
   border-radius: 10px;
}
.home h2{
    margin-right: 70%;
}
.home hr{
    background: -webkit-gradient(linear, left top, right top, from(rgb(235, 240, 233)),color-stop(rgb(211, 201, 201)), to(rgb(234, 241, 236)));
    background: -webkit-linear-gradient(left, rgb(235, 240, 233),rgb(211, 201, 201), rgb(234, 241, 236));
    background: -o-linear-gradient(left, rgb(235, 240, 233),rgb(211, 201, 201), rgb(234, 241, 236));
    background: linear-gradient(to right, rgb(235, 240, 233),rgb(211, 201, 201), rgb(234, 241, 236));
    height: 1px;
    border-style: hidden;
}

  .intro{
    padding-top: 40px;  
    padding-left: 7vw;  
    max-width: 35vw;

  }

  .intro p{
      color: rgb(23, 48, 36);
      font-family: sans-serif;
      font-size: 16px;
      font-weight: lighter;
  }
  .description{
      padding-top: 100px;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-pack: space-evenly;
          justify-content: space-evenly;
  }
  .description h1{
      font-weight: lighter;
      font-size: 20px;
      color: rgb(39, 39, 35);
  }
  .description p1{
      font-size: 22px;
      font-weight: lighter;
  }
.makeline{
    padding-right: 10vw;
    border-right-style: inset;
    border-width: thin;
    border-color:rgb(216, 210, 210);
    height: 15vh; 
}
.dipanim{
    animation: mydip 8s infinite;
    -moz-animation: mydip 8s infinite;
    -webkit-animation: mydip 8s infinite;
    -o-animation: mydip 8s infinite;    
    color: rgb(5, 66, 5);
}


@media screen and (max-width: 800px) {
    .home{
        -ms-flex: .65 1;
            flex: .65 1;
        background-image: -webkit-gradient(linear, left top, right top, from(rgb(242, 250, 239)), color-stop(rgb(255, 255, 255)),to(rgb(248, 255, 235)));
        background-image: -webkit-linear-gradient(left, rgb(242, 250, 239), rgb(255, 255, 255),rgb(248, 255, 235));
        background-image: -o-linear-gradient(left, rgb(242, 250, 239), rgb(255, 255, 255),rgb(248, 255, 235));
        background-image: linear-gradient(to right, rgb(242, 250, 239), rgb(255, 255, 255),rgb(248, 255, 235));;
    }
    .intro{
        padding-left: 5vw; 
    }
    .intro p{
        font-size: 10px;
    }
    .description{
        padding-left: 30px;
        padding-top: 50px;
        max-width: 130px;
    }
    .description h1{
        font-size: 14px;
    }
    .description p1{
        font-size: 12px;
    }
}



























@keyframes mydip {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {font-weight: lighter ;}
    to {font-weight:bolder ;}
  }
   @-webkit-keyframes mydip {
    from {opacity: 0 ;}
    to {opacity: 1;}
    from {font-weight: lighter ;}
    to {font-weight:bolder ;}
  }


