.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  background-image: -webkit-linear-gradient(120deg, #ffffff 0%, #747573 100%);
  background-image: -o-linear-gradient(120deg, #ffffff 0%, #747573 100%);
  background-image: linear-gradient(-30deg, #ffffff 0%, #747573 100%);
  height: 100vh;
}


@media screen and (max-width: 800px) {
  .App{
    background-image: -webkit-linear-gradient(30deg, rgb(221, 228, 214),rgb(243, 243, 240));
    background-image: -o-linear-gradient(30deg, rgb(221, 228, 214),rgb(243, 243, 240));
    background-image: linear-gradient(60deg, rgb(221, 228, 214),rgb(243, 243, 240));
  }
}




